@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,300;0,400;0,500;1,300&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Roboto Mono", sans-serif;
  color: #252525;
  font-weight: 400;
  background-color: #f7f2e9;
  border-bottom: 1.3rem solid #ee4d1b;
  min-height: 100vh;
  padding: 3.2rem;
  padding-bottom: 5rem;
}
.container {
  max-width: 50rem;
  margin: 0 auto;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4.8rem;
}

/* Start Header */

.header h1 {
  color: #ee4d1b;
  text-transform: uppercase;
  text-align: center;
  font-size: 3rem;
  font-weight: 400;
  letter-spacing: 3px;
  position: relative;
}
@media (max-width: 767px) {
  .header h1 {
    font-size: 2rem;
  }
}
.header h1::before,
.header h1::after {
  display: block;
  content: "";
  height: 3px;
  width: 2.2rem;
  background-color: #ee4d1b;
  position: absolute;
  top: calc(50% - 1.5px);
}
.header h1::before {
  left: -10%;
}

.header h1::after {
  right: -10%;
}

/* End Header */

/* Start Menu */

.menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4rem;
}

.menu h2 {
  display: inline-block;
  padding: 0.7rem 0;
  border-top: 2px solid #252525;
  border-bottom: 2px solid #252525;
  font-size: 1.7rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 500;
}
.menu > p {
  text-align: center;
  line-height: 1.6;
  width: 80%;
}
.pizzas {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}
@media (max-width: 767px) {
  .pizzas {
    grid-template-columns: 1fr;
  }
}
.pizza {
  display: flex;
  gap: 3.2rem;
}
.pizza img {
  width: 8rem;
  aspect-ratio: 1;
  align-self: start;
}
.pizza div {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  padding: 0.4rem 0;
}
.pizza h3 {
  font-size: 1.2rem;
  font-weight: 400;
}

.pizza p {
  font-weight: 300;
  font-style: italic;
  margin-bottom: auto;
}
@media (max-width: 767px) {
  .pizza {
    flex-direction: column;
    gap: 1.5rem;
  }
  .pizza img {
    margin: 0 auto;
    width: 14rem;
  }
  .pizza div {
    text-align: center;
  }
}
.pizza.sold-out {
  color: #888;
}

.pizza.sold-out img {
  filter: grayscale();
  opacity: 0.8;
}
/* End Menu */

/* Start Footer */
.footer {
  font-size: 1.3rem;
}
.order {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 2.4rem;
}

.btn {
  color: inherit;
  font-family: inherit;
  border: none;
  font-size: 1.1rem;
  font-weight: 500;
  background-color: #ee4d1b;
  padding: 1rem 1.5rem;
  cursor: pointer;
  transition: all 0.2s;
}

.btn:hover {
  background-color: #dd6039;
}
/* End Footer */
